body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

p {
  margin: 0;
  padding: 0;
}

ul {
  margin: 0;
  padding: 0;
}

html, body {
  font-size: 14px;
}

@media (min-width: 320px) and (max-width: 800px) {
    .home-header {
        height: 13vh !important;
        display: block !important;
        min-height: 100px;
    }
    .menu-home {
        width: 100% !important;
    }
    .todo-main {
        height: 87vh !important;
    }
    .chat-main {
        height: 85vh !important;
    }
    .info-home {
        width: 90% !important;
        margin-left: 10px;
        margin-top: 10px;
    }
    .chat-input {
        height: auto !important;
    }
    .control-home {
        width: 20% !important;
    }
    .todo-input-container {
        height: auto !important;
    }
    .add-task-button {
        width: 100% !important;
        margin-top: 20px;
    }
    .chat-item {
        font-size: 16px !important;
    }
    .chat-component {
        min-width: 100px !important;
    }
}

@media (max-width: 960px) {
    .chat-buttons {
        font-size: 10px !important;
    }
}

.page-home {
    width: 100%;
    height: 100vh;
}

.todo-main {
    width: 100%;
    height: 93vh;
    display: flex;
}

.home-header {
    display: flex;
    height: 7vh;
}

.menu-home {
    display: flex;
    width: 25%;
    padding-top: 5px;
}

.info-home {
    display: flex;
    width: 75%;
    justify-content: space-between;
    margin-right: 40px;
    margin-top: 5px;
}

.control-home {
    display: flex;
    width: 10%;
    justify-content: space-between;
}

.user-name {
    font-size: 20px;
    margin-top: 5px;
}

.app-name {
    font-size: 25px;
    margin-top: 5px;
    margin-left: 45px;
}

.header-icons {
    font-size: 30px;
}

.counter {
    background: #9cc86b;
    padding: 5px 10px;
    border-radius: 50px;
    color: white;
    position: absolute;
    top: -5px;
    right: -15px;
}

.counter-messages {
    background: #e62416;
}

.button-logout {
    background: inherit;
    border: 1px lightgrey solid;
    border-radius: 5px;
    outline: none;
    padding: 5px;
    cursor: pointer;
    margin-left: 5px;
}

.button-header {
    width: 50px;
    height: 50px;
    background: inherit;
    border: 1px lightgray solid;
    border-radius: 5px;
    cursor: pointer;
    outline: none;

    position: absolute;
}
.todo-list-container {
    width: 100%;
    height: 100%;
    background: #f2f0f3;
    height: auto;
    padding: 15px;
}

.container-todo-item {
    background: white;
    border-radius: 15px;
    width: 100%;
    height: 100%;
    overflow-y: auto;
}

.container-item {
    display: flex;
    border-bottom: 2px dotted lightgray;
    width: 97%;
    margin: 10px auto;
    padding-bottom: 5px;
}

.item-check {
    width: 25px;
    height: 25px;
}

.item-text {
    width: 90%;
    max-width: 90%;
    overflow: hidden;
    text-overflow: ellipsis;
}

.content-update-item {
    display: flex;
}

.done-item {
    width: 2%;
}

.like-icon {
    color: rgb(126, 204, 10) !important;
}

.button-control {
    width: 25px;
    height: 25px;
    border: none;
    border-radius: 5px;
    margin-right: 5px;
    outline: none;
    cursor: pointer;
}

.button-approve {
    background: #6eb44c;
    color: white;
}
.todo-input-container {
    height: 40px;
    border-bottom: 1px solid lightgrey;
    margin-bottom: 10px;
    padding: 5px;
}

.todo-input {
    outline: none;
    height: 80%;
    border: none;
}

.input-new-value {
    outline: none;
}

.add-task-button {
    width: 150px;
    height: 30px;
    background: #72b14c;
    color: white;
    border: 1px #72b14c solid;
    border-radius: 5px;
    cursor: pointer;
    outline: none;
}

.chat-component {
    background: #282c34;
    width: 25%;
}

.chat-item {
    font-size: 26px;
    color: white;
    border: none;
    background: inherit;
    cursor: pointer;
    outline: none;
}

.chat-content {
    margin-left: 20px;
    margin-top: 10px;
}

.chat-container {
    width: 100%;
    background: lavender;
}

.chat-main {
    height: 92vh;
}

.chat-header {
    color: white;
    margin-left: 10px;
}

h2 {
    padding: 0;
    margin: 0;
}

.chat-header-container {
    background: black;
    width: 100%;
    height: 5%;
    padding: 5px 0;
}

.chat-messages {
    background: whitesmoke;
    padding-left: 20px;
    padding-top: 20px;
    padding-right: 20px;
    height: 87%;
    overflow-y: auto;
}

.chat-sending {
    height: 5%;
    background: lavender;
    display: flex;
    justify-content: space-around;
    padding-bottom: 5px;
}

.chat-send {
    background: rgb(9, 189, 9);
    border: 1px solid rgb(9, 189, 9);
}

.chat-input {
    width: 70%;
    border-radius: 10px;
    height: 30px;
    border: 1px white solid;
    outline: none;
    margin-top: 7px;
}

.chat-buttons {
    border-radius: 10px;
    outline: none;
    cursor: pointer;
    margin-top: 5px;
    color: white;
    width: 13%;
    font-size: 20px;
    height: 80%;
}

.chat-delete-all {
    background: rgb(211, 9, 9);
    border: 1px solid rgb(211, 9, 9);
}

.message-container {
    padding: 5px;
    background: rgb(82, 154, 236);
    border-radius: 10px;
}

.nickName-content {
    font-style: italic;
    margin-right: 5px;
}

.message-content {
    color: white;
}

.message-block {
    margin-top: 20px;
    display: flex;
}

.page {
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.form-login {
    width: 500px;
    border: 1px lightgray solid;
}

.header-container {
    width: 100%;
    height: 70px;
    background: #ad2e19;
    color: white;
}

.header-contant {
    display: flex;
    justify-content: space-between;
    padding: 25px;

    font-size: 1.28rem;
}

.icon-close {
    padding-top: 5px;
}

.button-submit {
    background: #ad2f19;
    color: white;
    border: 1px #ad2f19 solid;
    border-radius: 5px;
    outline: none;

    width: 60%;
    height: 40px;

    font-size: 1.5rem;
    margin-right: 10px;
}

.container-button {
    width: 80%;
    margin: 20px auto;
}

.button {
    cursor: pointer;
    outline: none;
}

.button-reset {
    background: inherit;
    color: #ad2f19;
    border: none;
    font-weight: 700;
    font-size: 1.1rem;
}


.container-input {
    display: flex;
    justify-content: space-between;
    width: 80%;
    margin: 20px auto;
    border: 1px solid lightgrey;
    height: 50px;
}

.login-input {
    border: none;
    width: 90%;
    outline: none;
    padding-left: 20px;
    font-size: 1.28rem;
}

.icon-input {
    margin: 15px;
}

